import React from 'react';
import "./nav.css";
import MenuList from '../../components/menuList';
import MenuSection from '../../components/menuSection';

//images
import icon1 from "../../assets/images/icon_1.png"
import icon2 from "../../assets/images/icon_2.png"
import icon3 from "../../assets/images/icon_3.png"
import icon4 from "../../assets/images/icon_4.png"
import icon5 from "../../assets/images/icon_5.png"
import pic1 from "../../assets/images/pic1.png"


class Nav extends React.Component{
    render(){
        return(
            <nav>

            <div className="logo_holder">
            <img src={pic1} className="nav_logo"/>
            </div>

            <MenuSection sectionTitle="Logo">
                <MenuList title="New Releases" icon={icon1}/>
                <MenuList title="Most Popular" icon={icon2}/>
                <MenuList title="Recommended" icon={icon3} />
                <MenuList title="Top Chart" icon={icon4} />
            </MenuSection>

            <br/><br/>
            <br/>
            <MenuSection sectionTitle="My Collection">
                <MenuList title="Bookmark" icon={icon4}/>
                <MenuList title="History" icon={icon5}/>
                <MenuList title="Subscriptions" icon={icon1}/>
            </MenuSection>
            <br/>
            <br/>
            </nav>

            
        )
            
    }
}
    
export default Nav